@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Oswald.woff2") format("woff2"), url("../fonts/Oswald.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Arial";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Arial.woff2") format("woff2"), url("../fonts/Arial.woff") format("woff");
  font-display: swap;
}
.wrapper {
  margin: 0 20px;
}
.wrapper--additional {
  position: relative;
}
.wrapper--slider {
  margin: 0;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 30px;
  }
  .wrapper--form {
    padding: 0 37px 0 41px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    margin: 0 110px;
  }
  .wrapper--slider {
    margin: 0;
  }
  .wrapper--form {
    padding: 0;
  }
  .wrapper--form-personality {
    margin-right: 0;
  }
  .wrapper--form-radio {
    margin-left: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.link {
  text-decoration: none;
  cursor: pointer;
}

.button {
  display: block;
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  color: #ffffff;
  background-color: #69b838;
  border: none;
  text-transform: uppercase;
  padding: 7px 44px;
  cursor: pointer;
  box-sizing: border-box;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--loading {
  background-color: #f2f2f2;
}
.button--loading:hover {
  background-color: #ebebeb;
}
.button--loading:active {
  color: rgba(68, 68, 68, 0.3);
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.title {
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  font-size: 36px;
  line-height: 36px;
  margin: 0;
  letter-spacing: 1px;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.image {
  display: block;
  max-width: 100%;
  object-fit: contain;
}

.page {
  height: 100%;
  font-family: "Arial", "Calibri", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.page__page-body {
  margin: 0;
}
@media (min-width: 1440px) {
  .page__page-body {
    margin: 0 auto;
  }
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #000000;
  background-color: #ffffff;
}
@media (min-width: 1440px) {
  .page-body {
    max-width: 1440px;
  }
}

.page-body__page-header {
  position: relative;
  min-height: 64px;
}
@media (min-width: 768px) {
  .page-body__page-header {
    margin: 14px 0 46px;
  }
  .page-body__page-header--catalog {
    margin: 14px 0 48px;
  }
}
@media (min-width: 1440px) {
  .page-body__page-header {
    margin: 53px 0 111px;
  }
  .page-body__page-header--index {
    margin-top: 0;
  }
}

.page-body__page-header--catalog::after,
.page-body__page-header--form::after {
  content: "";
  position: absolute;
  bottom: -1.5px;
  width: 100%;
  height: 1px;
  background-color: #cdcdcd;
}
@media (min-width: 768px) {
  .page-body__page-header--catalog::after,
.page-body__page-header--form::after {
    content: none;
  }
}
@media (min-width: 1440px) {
  .page-body__page-header--catalog,
.page-body__page-header--form {
    margin: 44px 0 57px;
  }
}

.page-header {
  display: flex;
  flex-direction: column;
  position: relative;
}
@media (min-width: 768px) {
  .page-header {
    flex-direction: row;
    align-items: center;
    column-gap: 64px;
  }
}
@media (min-width: 1440px) {
  .page-header {
    margin-right: 110px;
    justify-content: space-between;
  }
  .page-header--index {
    margin: 0 0 95px;
    padding: 43px 110px 0 0;
  }
  .page-header--index::after {
    content: "";
    position: absolute;
    width: 720px;
    height: 694px;
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index/promo-background-image-desktop.jpg");
    top: 0;
    right: 0;
    left: auto;
    background-size: cover;
    min-height: 100%;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page-header--index::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/index/promo-background-image-desktop@2x.jpg");
  }
}

.page-header__logo {
  padding: 10px 0 17px;
  cursor: pointer;
  width: 191px;
}
.page-header__logo:hover {
  opacity: 0.8;
}
.page-header__logo:active {
  opacity: 0.6;
}

.page-header__logo-image {
  display: block;
  width: 191px;
  height: 37px;
}
@media (min-width: 768px) {
  .page-header__logo-image {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1440px) {
  .page-header__logo-image {
    width: 202px;
    height: 59px;
  }
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-nav {
  position: relative;
}
@media (min-width: 768px) {
  .page-nav {
    margin-right: 0;
  }
}

.page-nav__toggle {
  position: absolute;
  top: -40px;
  right: 20px;
  z-index: 2;
  width: 24px;
  height: 16px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .page-nav__toggle {
    display: none;
  }
}

.page-nav--closed .page-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 7px 0 0 #000000, 0 14px 0 0 #000000;
}

.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  z-index: 1;
}
.page-nav--opened .page-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: rotate(-45deg);
}

.page-nav__list {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-nav__list {
    position: relative;
    background-color: transparent;
  }
}

.page-nav--nojs .page-nav__toggle {
  display: none;
}

@media (min-width: 320px) {
  .page-nav--closed .nav-list__item {
    display: none;
  }
}
@media (min-width: 768px) {
  .page-nav--closed .nav-list__item {
    display: block;
  }
}

.page-nav--nojs .nav-list__item {
  display: block;
}

@media (min-width: 768px) {
  .nav-list {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
  }
}
@media (min-width: 1440px) {
  .nav-list {
    max-width: 480px;
  }
}

.nav-list__item {
  position: relative;
  display: block;
  font-size: 20px;
  line-height: 20px;
  padding: 24px 24px 20px;
  text-align: center;
  letter-spacing: 1px;
}
.nav-list__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}
.nav-list__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #e6e6e6;
}
@media (min-width: 768px) {
  .nav-list__item {
    padding: 0;
  }
  .nav-list__item::before, .nav-list__item:last-child::after {
    content: none;
  }
}

.nav-list__link {
  font-family: "Oswald", "Calibri", "Helvetica", sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  color: #000000;
  cursor: default;
}
.nav-list__link:hover {
  opacity: 0.8;
  cursor: pointer;
}
.nav-list__link:active {
  opacity: 0.6;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav-list__link {
    font-size: 18px;
    line-height: 24px;
  }
  .nav-list__link--current:hover, .nav-list__link--current:active, .nav-list__link--current-index:hover, .nav-list__link--current-index:active {
    opacity: 1;
    cursor: default;
  }
  .nav-list__link--current::after, .nav-list__link--current-index::after {
    content: "";
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #69b838;
  }
}
@media (min-width: 768px) and (min-width: 1440px) {
  .nav-list__link--current::after, .nav-list__link--current-index::after {
    bottom: -6px;
  }
}
@media (min-width: 1440px) {
  .nav-list__link {
    font-size: 20px;
    line-height: 30px;
  }
  .nav-list__link--index {
    color: #ffffff;
  }
  .nav-list__link--current-index::after {
    background-color: #ffffff;
  }
}

